.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.photo {
  max-width: 100%;
  max-height: 80vh;
  transition: opacity 1s ease-in-out; /* Add fade-in animation */
}

.hidden {
  opacity: 0; /* Set opacity to 0 for hidden elements */
}

.caption {
  margin-top: 20px;
  text-align: center;
}

.main-text {
  font-size: 24px;
  font-weight: bold;
  font-family: 'Arial', sans-serif; /* Change font family */
  opacity: 0; /* Initial opacity for fade-in animation */
  animation: fadeIn 1.5s ease-in-out forwards; /* Add fade-in animation */
}

.sub-text {
  font-size: 18px;
  font-style: italic;
  font-family: 'Times New Roman', serif; /* Change font family */
  color: #888; /* Set a subtle color for sub-text */
  opacity: 0; /* Initial opacity for slide-up animation */
  animation: slideUp 1.5s ease-in-out forwards; /* Add slide-up animation */
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
